function Movies({ dataOf: { name, Date, time, about, src, imgSrc } }) {
  return (
    <div
      style={{
        display: "flex",
        width: "500px",
        height: "200",
        backgroundColor: "black",
        color: "white",
        padding: "2px",
        margin: "10px",
        textAlign: "center",
        justifyItems: "center"
      }}
    >
      <div>
        <div className="part1-div" style={{ display: "flex" }}>
          <div className="img-div">
            <img src={src} width="100px" />
          </div>
          <div className="About">
            <h1>{name}</h1>
            <p>{Date}</p>
            <p>{time}</p>
          </div>
        </div>
        <div className="summury">
          <p>{about}</p>
        </div>
        <div>
          <i class="fa-solid fa-heart" style={{ padding: "20px" }}></i>
          <i class="fa-solid fa-share-nodes"></i>
          <i class="fa-solid fa-message-middle"></i>
        </div>
      </div>
      <div style={{ width: "inherit" }}>
        <img
          src={imgSrc}
          style={{ width: "200%", height: "100%", objectfit: "contain" }}
        />
      </div>
    </div>
  );
}
export default Movies;
